.ps-container {

  @media @mobile-min1920{
    &:not(.scroll-right) > .ps-scrollbar-y-rail {
      left: 3px;
      opacity: 0.6;
      display: block;
      background-color: #e7eef1 !important;

      &:hover {
        background-color: #e7eef1;
        opacity: 0.9;
      }

      .ps-scrollbar-y {
        background-color: #b5ced5;

        &:hover {
          background-color: #bddde6 !important;
        }
      }
    }
