.clearfix:after{
  content: "";
  clear: both;
}
.navigation-main {
  position: absolute;
  z-index: 3;
  width: 100%;

  @media @mobile-min1920{
    padding: 40px 80px 40px 50px;
  }
  @media @mobile-max1920{
    padding: 30px 80px 30px 50px;
  }
  @media @mobile-max1440{
    padding: 30px 80px 30px 50px;
  }
  @media @mobile-max1366{
    padding: 30px 80px 30px 50px;
  }
  @media @mobile-max1280{
    padding: 30px 80px 30px 50px;
  }
  @media @mobile-max1280{
    padding: 30px 80px 30px 50px;
  }
  @media @mobile-max1024-landscape{
    padding: 30px 80px 30px 50px;
  }
  @media @mobile-max1024-portrait{
    padding: 30px 50px 30px 30px;
  }
  @media @mobile-max768-landscape{
    padding: 30px 80px 30px 50px;
  }
  @media @mobile-min568-max768{
    padding: 16px 38px 16px 38px;
  }
  @media @mobile-max568-portrait{
    padding: 30px 80px 30px 30px;
  }
  @media @mobile-max480{
    padding: 20px;
  }


  .left-side {
    float: left;

    @media @mobile-min1920{
      width: 50%;
    }
    @media @mobile-max1920{
      width: 40%;
    }
    @media @mobile-max1440{
      width: 40%;
    }
    @media @mobile-max1366{
      width: 40%;
    }
    @media @mobile-max1280{
      width: 23%;
    }

    @media @mobile-max1024-landscape{
      width: 20%;
    }
    @media @mobile-max1024-portrait{
      width: 50%;
    }

    @media @mobile-max768-landscape{
      width: 48%;
    }
    @media @mobile-min568-max768{
      width: 48%;
    }
    @media @mobile-max568-portrait{
      width: 50%;
    }
    @media @mobile-max480{
      width: 47%;
    }

    .logo {
      width: 110px;
      height: 27px;
      display: block;
      background: url('../images/logo/logo.png');
      background-repeat: no-repeat;
      background-size: 100% 100%;
      animation: pulse 2s 1;
      -moz-animation: pulse 2s 1;



      @keyframes pulse {
        0% {
          background: url('../images/logo/logo.png') no-repeat 0 0;
          background-size: 100% 100%;
          width: 110px;
          height: 27px;

        }
        50% {
          background: url('../images/logo/logo-no-button.png') no-repeat 0 0;
          background-size: 100% 100%;
          width: 110px;
          height: 27px;

        }
        100% {
          background: url('../images/logo/logo.png') no-repeat 0 0;
          background-size: 100% 100%;
          width: 110px;
          height: 27px;
        }
      }

      @keyframes pulse-opened {
        0% {
          background-color: #fff;
        }
        50% {
          background: none;
        }
        100% {
          background-color: #fff;
        }
      }
    }
  }
  .right-side {
    float: left;

    @media @mobile-min1920{
      width: 50%;
    }
    @media @mobile-max1920{
      width: 60%;
    }
    @media @mobile-max1440{
      width: 60%;
    }
    @media @mobile-max1366{
      width: 60%;
    }
    @media @mobile-max1280{
      width: 77%;
    }

    @media @mobile-max1024-landscape{
      width: 80%;
    }


  }

  .navigation-bars {
    display: none;
  }

  .languages {
    display: none !important;

  }

  .languages-mobile {
    display: none;
  }
}

.nav-box {
  position: fixed;
  text-align: center;
  overflow: hidden;
  z-index: -1;
  display: none;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: #fff;

  ul {
    position: relative;
    list-style: none;
    align-items: center;
    margin: 0;
    padding: 0;
    height: 50%;

    @media @mobile-max1024-portrait{
      top: 20%;
    }

    @media @mobile-max768-landscape{

    }
    @media @mobile-min568-max768{

    }
    @media @mobile-max568-portrait{
      top: 20%;
    }
    @media @mobile-max480{

    }
  }

  li {
    align-items: center;
    font-family: 'Brandon Black';

    &:first-child {
      margin-bottom: 20px;

      //@media @mobile-min1920{
      //
      //}
      //@media @mobile-max1920{
      //
      //}
      //@media @mobile-max1440{
      //
      //}
      //@media @mobile-max1366{
      //
      //}
      //@media @mobile-max1280{
      //
      //}

      //@media @mobile-max1024-landscape{
      //
      //}
      @media @mobile-max1024-portrait{
        margin-bottom: 20px;
      }

      @media @mobile-max768-landscape{
        margin-bottom: 20px;
      }
      @media @mobile-min568-max768{
        margin-bottom: 20px;
      }
      @media @mobile-max568-portrait{
        margin-bottom: 20px;
      }
      @media @mobile-max480{
        margin-bottom: 20px;
      }
      @media only screen and (min-width: 320px) and (max-width: 374px) and (orientation: portrait) {
        margin-bottom: 60px;
      }
      @media only screen and (min-width: 375px) and (max-width: 414px) and (orientation: portrait) {
        margin-bottom: 120px;
      }
      @media only screen and (min-width: 414px) and (max-width: 480px) and (orientation: portrait){
        margin-bottom: 120px;
      }
    }

    h1 {
      color: #1c9cbc;
      font-size: 3em;

      @media @mobile-max1024-portrait{
        font-size: 3em;
      }

      @media @mobile-max768-landscape{

      }
      @media @mobile-min568-max768{
        font-size: 2em;
      }
      @media @mobile-max568-portrait{

      }
      @media @mobile-max480{

      }
    }

    a {
      text-decoration: none;
      font-size: 1.5em;
      text-transform: uppercase;
      color: #000;

      &:hover, &.active {
        color: #ff7d00;
      }
    }
  }
}

#toggle-nav-label {
  color: #000000;
  font-size: 16px;
  cursor: pointer;
  float: right;
  top: 5px;
  position: relative;
}

#toggle-nav {
  display: none;
}

#toggle-nav:checked {

  & ~ .nav-box {
    display: block;
    z-index: 400;
  }

}

#toggle-nav:checked ~ #toggle-nav-label {
  background: #fff;
  color: rgba(0, 0, 0, 0.8);
}

ul.nav {
  float: right;
  list-style: none;
  margin-top: 5px;

  @media @mobile-min1920{
    padding-right: 50px;
  }
  @media @mobile-max1920{
    padding-right: 20px;
  }
  @media @mobile-max1440{
    padding-right: 20px;
  }
  @media @mobile-max1366{

  }
  @media @mobile-max1280{

  }

  @media @mobile-max1024-landscape{

  }
  @media @mobile-max1024-portrait{

  }

  @media @mobile-max768-landscape{

  }
  @media @mobile-min568-max768{

  }
  @media @mobile-max568-portrait{

  }
  @media @mobile-max480{

  }

  li {
    float: left;
    a {
      padding: 0 26px;
      color: #131313;
      font-family: 'Brandon Black';
      text-transform: uppercase;
      font-size: 14px;
      opacity: .6;
      text-decoration: none;
      letter-spacing: 0.025em;


      &:hover, &.active {
        color: #ff7d00;
        opacity: 1;
      }
    }
  }
}

@media @mobile-max1024-portrait{
  .navigation-main {

    .logo {
      width: 100px;
      height: 18px;
      background: url('../images/logo/logo.png') no-repeat 0 0;
      background-size: contain;
      animation: none;
    }

    .left-side {
      z-index: 500;

    }

    .navigation-bars {
      display: block;
      float: left;

      i {
        align-self: center;
        font-size: 24px;
      }
    }
    ul.nav {
      display: none;

      &.open {
        display: flex;
        position: absolute;
        margin: 0 auto;
        background: rgb(255, 255, 255, .3);
        width: 100%;
        left: 0;
        text-align: center;
        height: 100%;
        justify-content: center;

      }
    }
  }
}


