#index {
  height: 100%;
  background-color: #eff8f9;
  background-image: url(../images/backgrounds/index_edit.jpg);
  //background-image: transparent ;
  background-repeat: no-repeat;
  background-size: cover;


  @media @mobile-min1920{
    background-color: #eff8f9;
  }
  @media @mobile-max1920{
    background-position: 59% 0;
    background-size: 112%;
  }
  @media @mobile-max1440{
    background-position: 59% 0;
    background-size: 112%;
  }

  @media @mobile-max1366{
    background-position: 59% 0;
    background-size: 136%;
  }
  @media @mobile-max1280{
    background-position: 59% 0;
    background-size: 136%;
  }

  @media @mobile-ipadPro{
    background-position: 59% 0;
    background-size: 136%;
  }

  @media @mobile-max1024-landscape{
    background-position: 59% 0;
    background-size: 136%;
  }
  @media @mobile-max1024-portrait{
    background-position: 38% 0;
    background-size: 239%;
  }

  @media @mobile-max768-landscape{
    background: url('../images/backgrounds/index_1136_640.jpg');
    background-position: 61% 52%;
    background-size: 129%;
  }
  @media @mobile-min568-max768{
    background: url('../images/backgrounds/index_1136_640.jpg');
    background-position: 61% 52%;
    background-size: 129%;
  }

  @media @mobile-max568-portrait{
    background: url('../images/backgrounds/index_640_1136.jpg');
    background-position: 0 0;
    background-size: 100%;
  }
  @media @mobile-max480{
    background: url('../images/backgrounds/index_640_1136.jpg');
    background-position: 0 -30px;
    background-size: 100%;
    background-repeat: no-repeat;

  }


  .clearfix:after {
    content: "";
    clear: both;
  }

  .content {
    height: 100%;
    padding-bottom: 70px;

    @media @mobile-min1920{
      padding-bottom: 70px;
    }
    @media @mobile-max1920{
      padding-bottom: 70px;
    }
    @media @mobile-max1440{
      padding-bottom: 50px;
    }
    @media @mobile-max1366{
      padding-bottom: 70px;
    }
    @media @mobile-max1280{
      padding-bottom: 70px;
    }

    @media @mobile-max1024-landscape{
      padding-bottom: 50px;
    }
    @media @mobile-max1024-portrait{
      padding-bottom: 70px;
    }

    @media @mobile-max768-landscape{

    }
    @media @mobile-min568-max768{
      padding-bottom: 70px;
    }
    @media @mobile-max568-portrait{
      padding-bottom: 70px;
    }
    @media @mobile-max480{
      padding-bottom: 70px;
    }

    .divider{
      height: 40%;

      @media @mobile-min1920{
        height: 40%;
      }
      @media @mobile-max1920{
        height: 40%;
      }
      @media @mobile-max1440{
        height: 36%;
      }
      @media @mobile-max1366{
        height: 40%;
      }
      @media @mobile-max1280{
        height: 40%;
      }

      @media @mobile-max1024-landscape{
        height: 40%;
      }
      @media @mobile-max1024-portrait{
        height: 75%;
      }

      @media @mobile-max768-landscape{
        height: 30%;
      }
      @media @mobile-min568-max768{
        height: 40%;
      }
      @media @mobile-max568-portrait{
        height: 75%;
      }
      @media @mobile-max480{
        height: 70%;
      }

    }

    .main-content {
      height: 60%;

      @media @mobile-min1920{
        height: 60%;
        padding: 30px 100px 30px 50px;
      }
      @media @mobile-max1920{
        height: 60%;
        padding: 30px 80px 20px 50px;
      }
      @media @mobile-max1440{
        height: 64%;
        padding: 30px 80px 20px 50px;
      }
      @media @mobile-max1366{
        height: 60%;
        padding: 30px 60px 20px 50px;
      }
      @media @mobile-max1280{
        height: 60%;
        padding: 10px 60px 10px 20px;
      }

      @media @mobile-max1024-landscape{
        height: 60%;
        padding: 30px 70px 20px 50px;
      }
      @media @mobile-max1024-portrait{
        height: 25%;
        padding: 20px 20px 20px 20px;
      }



      @media @mobile-max768-landscape{
        height: 25%;
        padding: 20px 20px 20px 20px;
      }

      @media @mobile-min568-max768{
        height: 60%;
        padding: 20px 20px 20px 20px;
      }
      @media @mobile-max568-portrait{
        height: 64%;
      }
      @media @mobile-max480{
        height: 24%;
      }

      .left {
        float: left;
        min-height: 50px;
        display: block;

        @media @mobile-min1920{
          width: 60%;
        }
        @media @mobile-max1920{
          width: 54%;
        }
        @media @mobile-max1440{
          width: 54%;
        }
        @media @mobile-max1366{
          width: 54%;
        }
        @media @mobile-max1280{
          width: 54%;
        }

        @media @mobile-max1024-landscape{
          width: 54%;
        }
        @media @mobile-max1024-portrait{
          display: none;
        }

        @media @mobile-max768-landscape{

        }
        @media @mobile-min568-max768{
          width: 45%;
        }

        @media @mobile-max568-portrait{
          display: none;
        }
        @media @mobile-max480{
          display: none;
        }
      }
      .right {
        float: left;
        height: auto;
        display: block;

        @media @mobile-min1920{
          width: 40%;
          padding-right: 100px;
        }
        @media @mobile-max1920{
          width: 46%;
          padding-right: 50px;
        }
        @media @mobile-max1440{
          width: 46%;
        }
        @media @mobile-max1366{
          width: 46%;
        }
        @media @mobile-max1280{
          width: 46%;
        }

        @media @mobile-max1024-landscape{
          width: 46%;
        }
        @media @mobile-max1024-portrait{
          width: 100%;
          padding: 0 80px;
        }

        @media @mobile-max768-landscape{

        }
        @media @mobile-min568-max768{
          width: 55%;
        }
        @media only screen and (min-width: 736px) and (max-width: 767px) and (orientation: landscape){
          width: 44%;
        }
        @media @mobile-max568-portrait{
          width: 100%;
          padding: 0 80px;
        }
        @media @mobile-max480{
          width: 100%;
          padding: 20px 30px 0;
        }

      }

      .heading {
        color: #249fbe;
        letter-spacing: 0.025em;
        text-transform: uppercase;
        font-family: 'Brandon Black';

        @media @mobile-min1920{
          font-size: 80.5px;
        }
        @media @mobile-max1920{
          font-size: 80.5px;
        }
        @media @mobile-max1440{
          font-size: 80.5px;
        }
        @media @mobile-max1366{
          font-size: 80.5px;
        }
        @media @mobile-max1280{
          font-size: 60px;
        }


        @media @mobile-max1024-landscape{
          font-size: 60px;
        }
        @media @mobile-max1024-portrait{
          font-size: 68px;
        }


        @media @mobile-max768-landscape{
          font-size: 46px;
        }

        @media @mobile-min568-max768{
          font-size: 36px;
        }
        @media @mobile-max568-portrait{
          font-size: 36px;
        }
        @media @mobile-max480{
          font-size: 40px;
        }
        @media only screen and (min-width: 320px) and (max-width: 374px) and (orientation: portrait) {
          font-size: 30px;
        }


        .row {
          @media @mobile-min1920{
            line-height: 80px;
          }
          @media @mobile-max1920{
            line-height: 80px;
          }
          @media @mobile-max1440{
            line-height: 80px;
          }
          @media @mobile-max1366{
            line-height: 80px;
          }
          @media @mobile-max1280{
            line-height: 60px;
          }

          @media @mobile-max1024-landscape{
            line-height: 60px;
          }
          @media @mobile-max1024-portrait{
            line-height: 60px;
          }

          @media @mobile-max768-landscape{
            line-height: 46px;
          }
          @media @mobile-min568-max768{
            line-height: 40px;
          }
          @media @mobile-max568-portrait{
            line-height: 40px;
          }
          @media @mobile-max480{
            line-height: 40px;
          }
          @media only screen and (min-width: 320px) and (max-width: 374px) and (orientation: portrait) {
            line-height: 30px;
          }

          &:first-child {
            //margin-top: 100px;
            //padding-left: 110px;
          }

          .box {

            &.align-right {
              text-align: right;
            }
          }
        }
      }
    }
  }
}

.side-nav {
  position: absolute;
  top: 0;
  right: 40px;
  height: 100%;
  border-left: 1px solid #cadbe0;
}




