      .headline {
        color: #249fbe;
        text-transform: uppercase;
        font-family: 'Brandon Black';
        line-height: 1.2;
        z-index: 3;


        @media @mobile-min1920{
          width: 78%;
          font-size: 70px;
          margin-bottom: 50px;
          margin-left: 134px;
        }
        @media @mobile-max1920{
          font-size: 62px;
          margin-bottom: 30px;
          margin-left: 100px;
        }
        @media @mobile-max1440{
          font-size: 52px;
