.close_article{
  background: #fff;
  position: fixed;
  width: 72px;
  height: 60px;
  background: url("../images/btn_close.png") center bottom;
  background-size: 100% auto;
  top: 0;
  right: 6px;
  z-index: 150;

  @media @mobile-min1920{
    display: none;
  }
  @media @mobile-max1920{
    display: none;
  }
  @media @mobile-max1440{
    display: none;
  }
  @media @mobile-max1366{
    display: none;
  }
  @media @mobile-max1280{
    display: none;
  }

  @media @mobile-max1024-landscape{
    display: none;
  }
  @media @mobile-max1024-portrait{
    display: block;
  }

  @media @mobile-max768-landscape{
    display: block;
  }
  @media @mobile-min568-max768{
    display: block;
  }
  @media @mobile-max568-portrait{
    display: block;
  }
  @media @mobile-max480{
    display: block;
  }

