#portfolio {
  background: url(../images/backgrounds/blog.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-color: #fff;
  height: 100%;

  .clearfix:after {
    content: "";
    clear: both;
  }

  .content {
    height: 100%;
    padding-bottom: 70px;

    @media @mobile-min1920{
      padding-bottom: 70px;
    }
    @media @mobile-max1920{
      padding-bottom: 70px;
    }
    @media @mobile-max1440{
      padding-bottom: 50px;
    }
    @media @mobile-max1366{
      padding-bottom: 70px;
    }
    @media @mobile-max1280{
      padding-bottom: 70px;
    }

    @media @mobile-max1024-landscape{
      padding-bottom: 50px;
    }
    @media @mobile-max1024-portrait{
      padding-bottom: 70px;
    }

    @media @mobile-max768-landscape{

    }
    @media @mobile-min568-max768{
      padding-bottom: 70px;
    }
    @media @mobile-max568-portrait{
      padding-bottom: 70px;
    }
    @media @mobile-max480{
      padding-bottom: 70px;
    }

    .divider {
      height: 14%;

      @media @mobile-min1920{
        height: 14%;
      }
      @media @mobile-max1920{
        height: 12%;
      }
      @media @mobile-max1440{
        height: 12%;
      }
      @media @mobile-max1366{
        height: 12%;
      }
      @media @mobile-max1280{
        height: 12%;
      }

      @media @mobile-max1024-landscape{
        height: 12%;
      }
      @media @mobile-max1024-portrait{
        height: 8%;
      }

      @media @mobile-max768-landscape{

      }


      @media @mobile-min568-max768{
        height: 12%;
      }
      @media @mobile-max568-portrait{
        height: 10%;
      }
      @media @mobile-max480{
        height: 8%;
      }
    }
    .headline {
      color: #249fbe;
      text-transform: uppercase;
      font-family: 'Brandon Black';
      display: block;
      height: 13%;

      @media @mobile-min1920{
        font-size: 80.5px;
        margin-left: 290px;
        height: 26%;
      }
      @media @mobile-max1920{
        font-size: 80.5px;
        margin-left: 150px;
        height: 28%;
      }
      @media @mobile-max1440{
        font-size: 58px;
        height: 32%;
        margin: 46px 0 20px 150px;
      }

      @media @mobile-max1366{
        font-size: 64px;
        margin-left: 150px;
        height: 32%;
      }
      @media @mobile-max1280{
        font-size: 58px;
        margin-left: 46px;
        height: 26%;
        margin-top: 50px;
      }

      @media @mobile-max1024-landscape{
        font-size: 58px;
        margin-left: 46px;
        height: 26%;
        margin-top: 42px;

      }
      @media @mobile-max1024-portrait{
        font-size: 58px;
        margin-left: 30px;
        height: 8%;
        margin-top: 42px;
      }

      @media @mobile-max768-landscape{

      }

      @media @mobile-min568-max768{
        font-size: 28px;
        margin-left: 20px;
        height: 10%;
      }
      @media @mobile-max568-portrait{
        font-size: 28px;
        margin-left: 30px;
        height: 6%;
      }
      @media @mobile-max480{
        font-size: 28px;
        margin-left: 20px;
        height: 6%;
      }

    }

    .main-content {
      height: 73%;
      border-top: 1px solid #cddee3;

      @media @mobile-min1920{
        height: 60%;
        padding: 0 100px 30px 50px;
      }
      @media @mobile-max1920{
        height: 60%;
        padding: 0 80px 20px 50px;
      }
      @media @mobile-max1440{
        height: 62%;
        padding: 0 80px 20px 50px;
      }
      @media @mobile-max1366{
        height: 62%;
        padding: 0 60px 20px 50px;
      }
      @media @mobile-max1280{
        height: 62%;
        padding: 0 60px 10px 20px;
      }

      @media @mobile-max1024-landscape{
        height: 62%;
        padding: 0 70px 20px 50px;
      }
      @media @mobile-max1024-portrait{
        height: 70%;
        padding: 0 30px 20px 30px;
        border-top: none;
        overflow-y: scroll;
      }

      @media @mobile-max768-landscape{
        height: 25%;
        padding: 0 20px 20px 20px;
        border-top: none;
        overflow-y: scroll;
      }


      @media @mobile-min568-max768{
        height: 50%;
        padding: 0 20px 20px 20px;
        overflow-y: scroll;
        border-top: none;
      }
      @media @mobile-max568-portrait{
        height: 70%;
        overflow-y: scroll;
        border-top: none;
        padding: 0 30px 20px 30px;
      }
      @media @mobile-max480{
        height: 70%;
        border-top: none;
        padding: 0 20px 20px 20px;
        overflow-y: scroll;
      }

      section.selection_content {

        ul.portfolios {
          width: 20%;
          z-index: 1;
          position: relative;
          transition: top .1s linear;
          height: 100%;
          float: left;

          @media @mobile-min1920{
            margin-top: -31px;
            width: 20%;
          }
          @media @mobile-max1920{
            margin-top: -31px;
            width: 25%;
          }
          @media @mobile-max1440{
            margin-top: -31px;
            width: 20%;
          }
          @media @mobile-max1366{
            margin-top: -31px;
            width: 20%;
          }
          @media @mobile-max1280{
            margin-top: -31px;
            width: 25%;
          }

          @media @mobile-max1024-landscape{
            margin-top: -31px;
            width: 25%;
          }
          @media @mobile-max1024-portrait{
            display: none;
          }

          @media @mobile-max768-landscape{
            display: none;
          }
          @media @mobile-min568-max768{
            display: none;
          }
          @media @mobile-max568-portrait{
            display: none;
          }
          @media @mobile-max480{
            display: none;
          }

          li {
            z-index: 1;
            margin-top: 20px;

            a {
              color: #000;
              font-family: 'Brandon Black';
              font-size: 14px;
              text-decoration: none;
              text-transform: uppercase;

              &:hover {
                color: #ff7d00;
              }
            }

            &.active {
              a {
                color: #ff7d00;
              }
            }
          }
        }

        .portfolios_content {
          width: 80%;
          float: left;
          z-index: 1;

          @media @mobile-min1920{
            width: 80%;
          }
          @media @mobile-max1920{
            width: 75%;
          }
          @media @mobile-max1440{
            width: 80%;
          }
          @media @mobile-max1366{
            width: 80%;
          }
          @media @mobile-max1280{
            width: 75%;
          }

          @media @mobile-max1024-landscape{
            width: 75%;
          }
          @media @mobile-max1024-portrait{
            width: 100%;
          }

          @media @mobile-max768-landscape{
            width: 100%;
          }
          @media @mobile-min568-max768{
            width: 100%;
          }
          @media @mobile-max568-portrait{
            width: 100%;
          }
          @media @mobile-max480{
            width: 100%;
          }
        }

        .article-holder {
          z-index: 1;
        }

        article {
          width: 100%;
          z-index: 1;
          display: none;
          transition: all 1s linear;

          &.active {
            display: block;

            @media @mobile-min1920{
              margin-top: -70px;
            }
            @media @mobile-max1920{
              margin-top: -110px;
            }
            @media @mobile-max1440{
              margin-top: -100px;
            }
            @media @mobile-max1366{
              margin-top: -110px;
            }
            @media @mobile-max1280{
              margin-top: -60px;
            }

            @media @mobile-max1024-landscape{
              margin-top: -100px;
            }
            @media @mobile-max1024-portrait{

            }


            @media @mobile-max768-landscape{

            }
            @media @mobile-min568-max768{

            }
            @media @mobile-max568-portrait{

            }
            @media @mobile-max480{

            }
          }

          @media @mobile-max1024-portrait{
            display: block;
          }

          @media @mobile-max768-landscape{
            display: block;
          }
          @media @mobile-min568-max768{
            display: block;
          }
          @media @mobile-max568-portrait{
            display: block;
          }
          @media @mobile-max480{
            display: block;
          }

          .portfolio-transition {
            opacity: 1;
            transition: all .2s linear;
            &.out {
              opacity: 0;
            }
          }

          .article_content {
            float: left;
            width: 50%;

            @media @mobile-min1920{
              width: 50%;

            }
            @media @mobile-max1920{
              width: 60%;
            }
            @media @mobile-max1440{
              width: 50%;
            }
            @media @mobile-max1366{
              width: 60%;
            }
            @media @mobile-max1280{
              width: 60%;
            }

            @media @mobile-max1024-landscape{
              width: 60%;
            }
            @media @mobile-max1024-portrait{
              width: 100%;
              height: 100%;
              margin-left: 0;
            }

            @media @mobile-max768-landscape{
              height: 100%;
              width: 100%;

            }
            @media @mobile-min568-max768{
              height: 100%;
              width: 100%;
            }
            @media @mobile-max568-portrait{
              height: 100%;
              width: 100%;
            }
            @media @mobile-max480{
              width: 100%;
              height: 100%;
            }

            .image-mobile {
              display: none;

              @media @mobile-max1024-portrait{
                display: block;
                margin: 0 auto;

                img {
                  max-width: 100%;
                  max-height: 100%;
                }
              }

              @media @mobile-max768-landscape{
                display: block;
                margin: 0 auto;
                img {
                  max-width: 100%;
                  max-height: 100%;
                }
              }
              @media @mobile-min568-max768{
                display: block;
                margin: 0 auto;
                img {
                  max-width: 100%;
                  max-height: 100%;
                }
              }
              @media @mobile-max568-portrait{
                display: block;
                margin: 0 auto;
                img {
                  max-width: 100%;
                  max-height: 100%;
                }
              }
              @media @mobile-max480{
                display: block;
                margin: 0 auto;
                img {
                  max-width: 100%;
                  max-height: 100%;
                }
              }

            }

            div {
              h4 {
                color: #009ec0;
                font-family: 'Brandon Black';
                margin: 0;
                align-self: center;
                text-transform: uppercase;

                @media @mobile-min1920{
                  font-size: 34px;
                  height: 100px;
                }
                @media @mobile-max1920{
                  font-size: 34px;
                  height: 100px;
                }
                @media @mobile-max1440{
                  font-size: 34px;
                  height: 100px;
                }
                @media @mobile-max1366{
                  font-size: 34px;
                  height: 100px;
                }
                @media @mobile-max1280{
                  font-size: 34px;
                  height: 100px;
                }

                @media @mobile-max1024-landscape{
                  font-size: 34px;
                  height: 100px;
                }
                @media @mobile-max1024-portrait{
                  margin: 5px 0;
                  font-size: 40px;
                  order: 0;
                }



                @media @mobile-max768-landscape{
                  margin: 5px 0;
                  font-size: 22px;
                  order: 0;

                }
                @media @mobile-min568-max768{
                  margin: 5px 0;
                  font-size: 22px;
                  order: 0;
                }
                @media @mobile-max568-portrait{
                  margin: 5px 0;
                  font-size: 22px;
                  order: 0;
                }
                @media @mobile-max480{
                  margin: 5px 0;
                  font-size: 22px;
                  order: 0;
                }

              }
            }

            p {
              font-family: 'Proxima Nova';
              color: #525a5d;
              line-height: 1.7;
              width: 80%;

              @media @mobile-min1920{
                margin-top: 50px;
                width: 80%;
              }
              @media @mobile-max1920{
                margin-top: 30px;
                width: 80%;
              }
              @media @mobile-max1440{
                margin-top: 30px;
                width: 80%;
              }
              @media @mobile-max1366{
                margin-top: 30px;
                width: 80%;
              }
              @media @mobile-max1280{
                margin-top: 30px;
                width: 95%;
              }

              @media @mobile-max1024-landscape{
                margin-top: 30px;
                width: 95%;
              }
              @media @mobile-max1024-portrait{
                width: auto;
                font-size: 16px;
              }

              @media @mobile-max768-landscape{
                width: auto;
                font-size: 12px;
              }

              @media @mobile-min568-max768{
                width: auto;
                font-size: 14px;
              }
              @media @mobile-max568-portrait{
                width: auto;
                font-size: 12px;
              }
              @media @mobile-max480{
                width: auto;
                font-size: 12px;
              }

            }
          }

          .image {
            float: left;
            width: 50%;
            position: relative;
            z-index: 2;

            @media @mobile-min1920{
              margin-top: -40px;
              width: 50%;
            }
            @media @mobile-max1920{
              margin-top: -20px;
              width: 40%;
            }
            @media @mobile-max1440{
              margin-top: -20px;
              width: 50%;
            }
            @media @mobile-max1366{
              margin-top: -20px;
              width: 40%;
            }
            @media @mobile-max1280{
              margin-top: -20px;
              width: 40%;
            }

            @media @mobile-max1024-landscape{
              margin-top: 0;
              width: 40%;
            }
            @media @mobile-max1024-portrait{
              display: none;
            }

            @media @mobile-max768-landscape{
              display: none;
            }
            @media @mobile-min568-max768{
              display: none;
            }
            @media @mobile-max568-portrait{
              display: none;
            }
            @media @mobile-max480{
              display: none;
            }

            img {
              max-width: 100%;
              max-height: 100%;
            }
          }
        }

      }
    }
  }
}
